<template>
    <div class="container degisn-main">
		<div class="row main-box">
			<div class="grid">
				<div class="col-md-3 col-sm-4 col-xs-12 my-box masonry-box" v-for="data in designData">
					<div class="thumbnail">
						<a data-fancybox="gallery" v-bind:href="data.img" class="thumbnail-box">
							<img v-bind:src="data.img" alt="" class="img-responsive thumbnail-img">
						</a>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
    export default{
        name:'Webdesign',
        data(){
            return{
                designData:[]
            }
        },
        mounted(){
			var _this=this;
        	var url='./static/js/design.data.js';
        	fetch(url,{method:'get'}).then((res)=>{
        		res.json().then(data=>{
        			_this.designData=data;
        		}).then(()=>{
					var $grid=$('.grid').masonry({
						itemSelector:'.masonry-box'
					})
					$grid.imagesLoaded().progress( function() {
						$grid.masonry('layout');
					})
				})
        	})
        }
    }
</script>
<style lang="scss" scope>
$yellow_color:#FFF100;
.degisn-main{
	flex:1 0 auto;
	padding-top: 20px;
	.main-box{
		display:flex;
		flex-wrap:wrap;
		.grid{
			width: 100vw;
		}
	}
	.my-box{
		.thumbnail{
			padding: 6px;
			border-radius: 0;
			border: none;
			.thumbnail-box{
				position: relative;
				overflow: hidden;
				.thumbnail-img{
					transition:all .3s;
				}
				.caption-box{
					width: 100%;
					height: 100%;
					padding: 14px;
					background-color: rgba(0,0,0,.5);
					position: absolute;
					top: 0;
					left: 0;
					color:#fff;
					opacity: 0;
					transition:all .5s;
					display: flex;
					align-items:center;
					.thumbnail-link{
						background-color: $yellow_color;
						color:#000;
						padding: .1rem 1rem;
						text-decoration: none;
						display: inline-block;
						margin-top: 2px;
					}
				}
			}
		}
	}
}
</style>


